import React, { useState } from "react";
import "./style.css";

const Button = (props) => {
  const {text} = props;
  const [count, setCount] = useState(1);


  const clickbutton = () => {
    setCount(count + 1);
    console.log('点击触发了');
  }
  return (
    <div>
      <button className="blue" onClick={clickbutton}>这是app3的 button</button>
      <div>props的text: {text}</div>
      <div>{count}</div>
    </div>
  );
};

export default Button;
